.header {
  height: 100vh;
  width: 100%;
  background: url(../images/promo-bg.jpg) bottom / cover no-repeat;

  &__container {
    height: 100%;
    max-width: 1024px;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .header__container.menu {
    @media (max-width: 590px) {
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__top {
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__bottom {
    width: 100%;
  }

  &__phone {
    padding: 8px 0;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;

    color: #fff;

    &:hover {
      border-bottom: 1px solid #fff;
    }

    @media (max-width: 590px) {
      display: none;
    }
  }
  &__phone.menu {
    @media (max-width: 590px) {
      display: block;

      position: absolute;
      bottom: 90px;

      &:last-child {
        bottom: 50px;
      }
    }
  }

  &__title {
    margin-bottom: 60px;

    font-weight: bold;
    font-size: 60px;
    line-height: 60px;

    letter-spacing: -1.5px;

    color: #fff;

    &::before {
      content: "";
      display: block;
      width: 35px;
      height: 2px;
      margin-bottom: 16px;

      background: #fff;
      border-radius: 2px;
    }

    @media (max-width: 425px) {
      font-size: 46px;
      line-height: 40px;
      margin-bottom: 56px;
    }
  }
  &__title.menu {
    @media (max-width: 590px) {
      display: none;
    }
  }

  &__nav {
    margin: 24px 0;

    @media (max-width: 590px) {
      display: none;
    }
  }
  &__nav.menu {
    @media (max-width: 590px) {
      margin: 48px 0;
      display: block;
    }
  }

  &__logo {
    @media (max-width: 590px) {
      max-width: 73px;
    }
  }
  &__logo.menu {
    @media (max-width: 590px) {
      align-self: center;
      margin-left: 24px;
    }
  }

  &__icon {
    display: none;
    cursor: pointer;

    @media (max-width: 590px) {
      display: block;
    }
  }
  &__icon-img.menu {
    @media (max-width: 590px) {
      display: none;
    }
  }
}

.header.menu {
  @media (max-width: 590px) {
    background-image: none;
    background-color: $color-blue;
    position: fixed;
  }
}
