.burger {
  height: 12px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__item {
    display: block;
    height: 2px;
    width: 18px;

    border-radius: 2px;
    background-color: #fff;
  }
}

.burger__item.menu {
  transition: transform 0.3s;

  &:first-child {
    transform: rotate(45deg);
    position: relative;
    top: calc(50% - 1px);
  }

  &:nth-child(2) {
    display: none;
  }

  &:last-child {
    transform: rotate(-45deg);
    position: relative;
    top: calc(-50% + 1px);
  }
}
